import React, { useState } from 'react';
import styles from './GrantApplication.module.css';

// import mpdf from '../../assetss/mpdf.pdf';
import ghid from '../../assetss/GHID DE FINANARE.docx';
import model from '../../assetss/Model de buget detaliat.docx';
import formular from '../../assetss/Formular de aplicare.docx';


const GrantApplication = () => {
  const [formData, setFormData] = useState({
    initiativeName: '',
    leaderName: '',
    leaderEmail: '',
    document: [],
    confirmation: false
  });

  const [errors, setErrors] = useState({});
  const [uploading, setUploading] = useState(false);

  const handleInputChange = (event) => {
    const { id, value, type, checked, files } = event.target;
    if (type === 'file') {
      // Add selected files to the existing document array
      setFormData(prevState => ({
        ...prevState,
        document: [...prevState.document, ...Array.from(files)]
      }));
    } else {
      setFormData({
        ...formData,
        [id]: type === 'checkbox' ? checked : value
      });
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    const newErrors = {};

    if (!formData.initiativeName) newErrors.initiativeName = 'Denumirea grupului este obligatorie.';
    if (!formData.leaderName) newErrors.leaderName = 'Numele liderului este obligatoriu.';
    if (!formData.leaderEmail || !/\S+@\S+\.\S+/.test(formData.leaderEmail)) newErrors.leaderEmail = 'Email-ul liderului este invalid.';
    if (!formData.document || formData.document.length === 0) newErrors.document = 'Trebuie s ncarci documentele necesare.';
    if (!formData.confirmation) newErrors.confirmation = 'Trebuie s confirmi c informaiile sunt corecte.';
    setErrors(newErrors);

    if (Object.keys(newErrors).length === 0) {
      setUploading(true);
      try {
        const documentUrls = [];

        for (const file of formData.document) {
          const formDataToUpload = new FormData();
          formDataToUpload.append('file', file);
          formDataToUpload.append('upload_preset', 'zmwzm6dh'); // Asigur-te c acest preset este corect

          const cloudinaryResponse = await fetch('https://api.cloudinary.com/v1_1/dbwamoqoc/upload', { // nlocuiete cu numele tu cloud
            method: 'POST',
            body: formDataToUpload
          });

          const cloudinaryData = await cloudinaryResponse.json();

          if (cloudinaryResponse.ok) {
            documentUrls.push(cloudinaryData.secure_url);
          } else {
            console.error('Eroare la ncrcarea fiierului:', cloudinaryData);
            alert('A aprut o eroare la ncrcarea fiierului. Te rugm s ncerci din nou mai trziu.');
            return;
          }
        }

        const formDataToSend = {
          initiativeName: formData.initiativeName,
          leaderName: formData.leaderName,
          leaderEmail: formData.leaderEmail,
          documentUrls,  // Trimite toate URL-urile fiierelor ncrcate
          confirmation: formData.confirmation ? 'true' : 'false'
        };

        const response = await fetch('https://formspree.io/f/mnnadzan', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(formDataToSend)
        });

        if (response.ok) {
          alert('Formularul a fost trimis cu succes!');
          setFormData({ initiativeName: '', leaderName: '', leaderEmail: '', document: [], confirmation: false });
        } else {
          const result = await response.json();
          console.error('Eroare la trimiterea datelor:', result);
          alert(`A aprut o eroare: ${result.errors ? result.errors.map(e => e.message).join(', ') : 'Necunoscut'}`);
        }
      } catch (error) {
        console.error('Eroare la trimiterea datelor:', error);
        alert('A aprut o eroare la trimiterea formularului. Te rugm s ncerci din nou mai trziu.');
      } finally {
        setUploading(false);
      }
    }
  };

  return (
    <section className={styles.container}>
      <div className={styles.textCenter}>
        <h2 className={styles.sectionHeading}>Apel de granturi pentru iniiativele de tineret din Municipiul Bli</h2>
        <p className={styles.sectionSubheading}>
          Programul de Granturi Mici este accesibil pentru orice grup de tineri cu vrsta cuprins ntre 14-25 de ani, din Bli, care vor s remedieze o problem din comunitate.
        </p>
      </div>
      <div className={styles.contactContainer}>
        <div className={styles.contactWrapper}>
          <p><strong>Ce proiecte pot fi finanate:</strong></p>
          <ul>
            <li>S rspund ntr-un mod inovator unei nevoi identificate la nivelul comunitii.</li>
            <li>S permit obinerea unor rezultate clare, vizibile.</li>
            <li>S aib un impact pe termen lung.</li>
            <li>S ofere condiii sigure de petrecere a timpului liber pentru tineri.</li>
            <li>S fie antrenante i plcute.</li>
          </ul>
          <p><strong>Pentru a participa, trebuie s:</strong></p>
          <ol>
            <li>Analizezi Ghidul de Finanare</li>
            <li>Completezi Formularul de Finanare</li>
            <li>Transmii Formularul pe pota <a href="mailto:pgm@studem.md">pgm@studem.md</a></li>
            <li>Confirmi finanarea din partea STUDEM</li>
            <li>Semnezi acordul de subgrant</li>
            <li>Implementai proiectul</li>
          </ol>
          <div className={styles.downloadLinks}>
            <div><a href={ghid} download>Ghidul de Finanare</a></div>
            <div><a href={formular} download>Formularul de Aplicare</a></div>
            <div><a href={model} download>Model de buget</a></div>
            {/* <div><a href={mpdf} download>Formularul de Raportare</a></div> */}
          </div>
        </div>
        <div className={styles.contactFormWrapper}>
          <form className={styles.contactForm} onSubmit={handleSubmit}>
            <div className={styles.formGroup}>
              <label htmlFor="initiativeName">Denumirea grupului de Iniiativ</label>
              <input
                type="text"
                id="initiativeName"
                name="initiativeName"
                value={formData.initiativeName}
                onChange={handleInputChange}
                className={styles.formControl}
              />
              {errors.initiativeName && <span className={styles.error}>{errors.initiativeName}</span>}
            </div>
            <div className={styles.formGroup}>
              <label htmlFor="leaderName">Liderul grupului de iniiativ</label>
              <input
                type="text"
                id="leaderName"
                name="leaderName"
                value={formData.leaderName}
                onChange={handleInputChange}
                className={styles.formControl}
              />
              {errors.leaderName && <span className={styles.error}>{errors.leaderName}</span>}
            </div>
            <div className={styles.formGroup}>
              <label htmlFor="leaderEmail">Adresa electronic a liderului grupului de iniiativ</label>
              <input
                type="email"
                id="leaderEmail"
                name="leaderEmail"
                value={formData.leaderEmail}
                onChange={handleInputChange}
                className={styles.formControl}
              />
              {errors.leaderEmail && <span className={styles.error}>{errors.leaderEmail}</span>}
            </div>
            <div className={styles.formGroup}>
              <label htmlFor="document">ncarc documentul/documentele</label>
              <input
                type="file"
                id="document"
                name="document"
                onChange={handleInputChange}
                className={styles.formControl}
                multiple
              />
              {errors.document && <span className={styles.error}>{errors.document}</span>}
              <ul>
                {formData.document && formData.document.map((file, index) => (
                  <li key={index}>{file.name}</li>
                ))}
              </ul>
            </div>
            <div className={styles.formGroup}>
              <label>
                <input
                  type="checkbox"
                  id="confirmation"
                  name="confirmation"
                  checked={formData.confirmation}
                  onChange={handleInputChange}
                />
                Confirm c informaia menionat n formularul de grant i mai sus este corect.
              </label>
              {errors.confirmation && <span className={styles.error}>{errors.confirmation}</span>}
            </div>
            <button type="submit" className={styles.submitButton} disabled={uploading}>
              {uploading ? 'Se ncarc...' : 'Trimite formularul de grant'}
            </button>
          </form>
        </div>
      </div>
    </section>
  );
};

export default GrantApplication;
